@use "global" as *;

.voice-card {
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
  background-color: $white;
}

.voice-card--aside {
  box-shadow: none;
}

.voice-card__inner {
  height: 100%;
  padding: rem(15) rem(16) rem(24);
  display: flex;
  flex-direction: column;
  gap: rem(16);
  @include mq(md) {
    padding: rem(24);
    gap: rem(40);
  }
}

.voice-card__head {
  flex-grow: 1;
  display: flex;
  gap: rem(16);
}

.voice-card__description {
  flex-grow: 1;
  margin-top: auto;
  @include mq(md) {
  }
}

.voice-card__meta {
  @include mq(md) {
    margin-bottom: rem(1);
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.voice-card__age {
  color: $black;
  font-size: rem(12);
  font-weight: $regular;
  line-height: 1.5;
  @include mq(md) {
    margin-bottom: rem(5);
  }
}

.voice-card__tag {
  margin-bottom: rem(4); // タイトルに余白をつけないため仕方なし
  color: $main-bg-color;
  font-size: rem(12);
  line-height: calc(rem(22) / rem(12));
  display: inline-block;
  border: 1px solid $main-bg-color;
  padding: rem(3) rem(11.5);
  @include mq(md) {
    padding: rem(5) rem(15);
  }
}

.voice-card__title {
  color: $black;
  font-weight: $normal;
  font-size: rem(16);
  line-height: 1.45;
  padding-bottom: rem(3.5);
  border-bottom: 1px solid $main-bg-color;
  @include mq(md) {
    font-size: rem(24);
    line-height: 1.5;
  }
}

.voice-card__img {
  margin-top: auto;
  aspect-ratio: 151 / 117;
  width: 100%;
  max-width: calc(rem(151) / rem(313) * 100%);
  position: relative;
  height: 100%;
  flex-shrink: 0;
  @include mq(md) {
    margin-top: auto;
    aspect-ratio: 180 / 140;
    min-width: 151px;
    max-width: rem(180);
  }
}

.voice-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.voice-card__img .is-color {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: $main-bg-color;
}

.voice-card__text {
  color: $black;
  font-size: rem(16);
  font-weight: $regular;
  line-height: 1.5;
}

.voice-card__img--rating {
  aspect-ratio: 294 / 218;
  max-width: rem(490);
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  @include mq(md) {
    aspect-ratio: 294 / 218;
    max-width: rem(294);
  }
}

.voice-card__meta--rating {
  @include mq(md) {
    display: block;
  }
}

.voice-card__age--rating {
  margin-top: rem(16);
  @include mq(md) {
    margin-top: rem(16);
  }
}

.voice-card__title--rating {
  margin-top: rem(8);
  padding-bottom: 0;
  border: none;
  font-size: rem(24);
  @include mq(md) {
    margin-top: rem(8);
    padding-bottom: 0;
  }
}