@use "global" as *;

/* ----- ローディングアニメーション ----- */
.loader{
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: $white;
  z-index: 8888;
  top: 0;
  left: 0;
}

.loader__title{
  z-index: 9999;
}

.loader__images{
  width: 100%;
  height: 100vh;
  display: flex;
  flex-wrap: nowrap;
  position: relative;
}

.loader__left-img, .loader__right-img{
  width: 50%;
  height: 100vh;
  position: absolute;
  top: 0;
  transform: translate(0, 100%);
  transition: transform cubic-bezier(0.21, 0.61, 0.35, 1) .6s; //https://cubic-bezier.com/#.21,.61,.35,1
  content: "";
  z-index: 9000;
}

.loader__left-img{
  left: 0;
}

.loader__right-img{
  right: 0;
}

.loader__left-img.slideUp{
  transform: translate(0, 0);
  transition-delay: 1.8s;
}

.loader__right-img.slideUp{
  transform: translate(0, 0);
  transition-delay: 1.9s;
}

.loader__left-img img, .loader__right-img img{
  aspect-ratio: 187.5 / 667;
  width: 100%;
  height: 100%;
  object-fit: cover;

  @include mq(md){
    aspect-ratio: 720 / 768;
  }
}

.loader__left-img img{
  object-position: top right;
}

.loader__right-img img{
  object-position: top left;
}