.button {
  display: inline-block;
  color: #fff;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  background-color: #408F95;
  line-height: 1.5714285714;
  padding: 0.9375rem 5.5rem 0.9375rem 2.5rem;
  flex-shrink: 1;
  border: 1px solid #408F95;
  position: relative;
  z-index: 1;
  margin-right: 0.25rem;
  margin-bottom: 0.25rem;
}

.button::before {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 100%;
  height: 100%;
  border: 1px solid #408F95;
  z-index: -100;
}

.button::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover,
.button:hover [type=submit] {
  color: #408F95;
  transition: 0.2s;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button span::after {
  background-color: #fff;
  content: "";
  display: inline-block;
  mask-image: url(../images/common/right.svg);
  mask-repeat: no-repeat;
  mask-size: cover;
  position: absolute;
  width: 2.5rem;
  height: 0.4375rem;
  top: 1.125rem;
  right: 2.5rem;
}

.button:hover span::after {
  background-color: #408F95;
}

.button--wtite {
  padding: 0.9375rem 6.0625rem 0.9375rem 2.5625rem;
  color: #408F95;
  background-color: #fff;
  border: 1px solid #fff;
}

.button--wtite::before {
  border: 1px solid #fff;
  z-index: -1;
}

.button--wtite::after {
  background: #408F95;
}

.button--wtite:hover {
  color: #fff;
}

.button--wtite span::after {
  background-color: #408F95;
}

.button--wtite:hover span::after {
  background-color: #fff;
}

.button--send {
  padding: 1rem 7rem 1rem 3.5rem;
}

.button--send span::after {
  background-color: #fff;
  content: "";
  display: inline-block;
  mask-image: url(../images/common/right.svg);
  mask-repeat: no-repeat;
  mask-size: cover;
  position: absolute;
  width: 2.5rem;
  height: 0.4375rem;
  top: 1.125rem;
  right: 3.5rem;
}

