@use "global" as *;

.blog-archive-accordion__title {
    color: $main-bg-color;
    font-family: $japanese-font-family;
    font-size: rem(24);
    font-weight: $regular;
    padding-left: rem(32);
    position: relative;
    cursor: pointer;
}

.blog-archive-accordion__title + .blog-archive-accordion__title {
    margin-top: rem(4);
}

.blog-archive-accordion__content {
  // display: none;
    padding-left: rem(24);
}

.blog-archive-accordion__content a {
    display: block;
}

.blog-archive-accordion__content.is-closed {
    display: none;
}

// トグル
.blog-archive-accordion__title::after {
    background-color: $main-bg-color;
    content: "";
    display: inline-block;
    mask-image: url(../images/common/toggle.svg);
    mask-repeat: no-repeat;
    mask-size: cover;
    position: absolute;
    width: rem(20);
    height: rem(24);
    top: 50%;
    left: rem(4);
    transform: translateY(-50%);
    transition: transform .3s ease-in-out, top .3s ease-in-out;
}

.blog-archive-accordion__title.is-open::after {
    top: 25%;
    transform: rotate(90deg);
}

