@use "global" as *;

.blog-card {
    box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
    background-color: $white;
}

.blog-card__inner {
    padding: rem(16) rem(16) rem(24);
    height: 100%;
    display: flex;
    flex-direction: column;
}

.blog-card__img {
    aspect-ratio: 301 / 201;
    overflow: hidden;
}

.blog-card__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.6s ease;
}

.blog-card__img:hover img {
    transform: scale(1.4);
}

.blog-card__date {
    margin-top: rem(24);
    display: inline-block;
    color: $black;
    font-family: $english-main-font-family;
    font-size: rem(12);
    line-height: 1.5;
    font-weight: $regular;
    @include mq(md) {
        margin-top: rem(24);
    }
}

.blog-card__title {
    color: $black;
    font-size: rem(16);
    font-weight: $normal;
    line-height: 1.4375;
    padding-bottom: rem(3);
    border-bottom: 1px solid $main-bg-color;
    flex-grow: 1;
    @include mq(md) {
        padding-bottom: rem(4);
    }
}

.blog-card__text {
    margin-top: rem(16);
    color: $black;
    font-size: rem(16);
    line-height: 1.5;
    @include mq(md) {
        letter-spacing: rem(0.1);
    }
}

.blog-card__wrapper {
    padding: rem(16);
    display: flex;
    gap: rem(16);
    align-items: flex-start;
}

.blog-card__img--ranking {
    width: 46%;
    aspect-ratio: 121 / 90;
    flex-shrink: 0;
}

.blog-card__date--ranking {
    margin-top: 0;
    @include mq(md) {
        margin-top: rem(0);
    }
}

.blog-card__title--ranking {
    margin-top: rem(4);
    border: none;
    padding-bottom: 0;
}

.blog-card--archive {
    margin-top: rem(-5);
    box-shadow: none;
    background-color: transparent;
    @include mq(md) {
    }
}

.blog-card__title--archive {
    margin-top: rem(8);
    font-size: rem(24);
    line-height: calc(35 / 24);
    @include mq(md) {
        margin-top: rem(8);
        font-size: rem(32);
        line-height: calc(46 / 32);
        padding-bottom: 0;
    }
}

.blog-card__body {
    margin-top: rem(-7);
}