@use "global" as *;

.campaign-card {
    box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
    background-color: $white;
    display: block;
    height: 100%;
}

.campaign-card__inner {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.campaign-card__img {
    aspect-ratio: 280 / 188;
    height: fit-content;
    @include mq(md) {
        aspect-ratio: 334 / 223;
    }
}

.campaign-card__img--aside {
    aspect-ratio: 294 / 188;
    @include mq(md) {
        aspect-ratio: 294 / 188;
    }
}

.campaign-card__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.campaign-card__body {
    padding: rem(16) rem(16) rem(24);
    height: 100%;
    display: flex;
    flex-direction: column;
    @include mq(md) {
        padding: rem(24) rem(16) rem(40);
    }
}

.campaign-card__body--aside {
    @include mq(md) {
        padding: rem(16) rem(23) rem(24);
    }
}

.campaign-card__body--page {
    @include mq(md) {
        padding: rem(24) rem(24) rem(40);
    }
}

.campaign-card__tag,
.campaign-card__price-low {
    color: $main-bg-color;
}

.campaign-card__title,
.campaign-card__detail,
.campaign-card__price-cost {
    color: $black;
}

.campaign-card__tag {
    font-size: rem(12);
    line-height: 1.83333;
    display: inline-block;
    border: 1px solid $main-bg-color;
    padding: rem(3) rem(11);
    margin-right: auto;
    @include mq(md) {
        padding: rem(5) rem(15);
    }
}

.campaign-card__title {
    margin-top: rem(4);
    font-size: rem(16);
    font-weight: $normal;
    line-height: 1.5;
    padding-bottom: rem(7.5);
    border-bottom: 1px solid $main-bg-color;
    flex-grow: 1;
}

.campaign-card__title--page {
    @include mq(md) {
        font-size: rem(24);
        line-height: calc(35 / 24);
        padding-bottom: rem(4);
    }
}

.campaign-card__title--aside {
    margin-top: rem(0);
    text-align: center;
    @include mq(md) {
        margin-top: rem(0);
        text-align: center;
    }
}

.campaign-card__detail {
    width: 100%;
    margin-top: rem(8);
    font-size: rem(14);
    line-height: 1.71429;
    text-align: center;
    @include mq(md) {
        margin-top: rem(16);
        font-size: rem(16);
        line-height: 1.5;
        gap: rem(25);
        text-indent: -1.1em;
    }
}

.campaign-card__detail--page {
    margin-top: rem(8);
    @include mq(md) {
        margin-top: rem(24);
        text-indent: -1.0em;
    }
}

.campaign-card__detail--aside {
    @include mq(md) {
        margin-top: rem(8);
        font-size: rem(14);
        font-weight: $regular;
        line-height: calc(24 / 14);
        text-indent: 0;
    }
}

.campaign-card__prices {
    margin-top: rem(8);
    display: flex;
    gap: rem(18);
    align-items: flex-end;
    justify-content: center;
    @include mq(md) {
        gap: rem(25);
    }
}

.campaign-card__prices--aside {
    @include mq(md) {
        gap: rem(16);
    }
}

.campaign-card__prices--page {
    @include mq(md) {
        gap: rem(40);
    }
}

.campaign-card__price-cost,
.campaign-card__price-low {
    font-family: $english-sub-font-family;
    font-weight: $bold;
    line-height: 1;
}

.campaign-card__price-cost {
    font-size: rem(20);
    line-height: 1.2;
    position: relative;
    @include mq(md) {
        font-size: rem(24);
    }
}

.campaign-card__price-cost--aside {
    @include mq(md) {
        font-size: rem(20);
        line-height: 1;
    }
}

// 赤い取消線
.campaign-card__price-cost::before {
    position: absolute;
    content: "";
    display: block;
    transform: rotate(0);
    background-color: red;
    width: 100%;
    height: rem(3);
    top: 40%;
    left: 0;
}

.campaign-card__price-low {
    font-size: rem(32);
    @include mq(md) {
        font-size: rem(40);
    }
}

.campaign-card__price-low--aside {
    @include mq(md) {
        font-size: rem(32);
        line-height: 1;
    }
}

.campaign-card__wrap {
    @include mq(md) {
        height: 100%;
        text-align: center;
        position: relative;
    }
}

.campaign-card__text {
    @include mq(md) {
        margin-top: rem(24);
        text-align: left;
        color: $black;
        font-family: $japanese-font-family;
        font-size: rem(16);
        font-weight: $regular;
        line-height: 1.5; /* 150% */
        flex-grow: 1;
        padding-bottom: rem(149);
    }
}

.campaign-card__content {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.campaign-card__date {
    @include mq(md) {
        margin-top: rem(32);
        color: $main-bg-color;
        font-family: $english-sub-font-family;
        font-size: rem(16);
        font-weight: $bold;
        line-height: calc(18 / 16);
        text-indent: -0.9em;
    }
}

.campaign-card__btn {
    @include mq(md) {
        margin-top: rem(16);
    }
}